import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
} from "react";
import s from "./SuperCheckbox.module.css";

// —Ç–∏–ø –ø—Ä–æ–ø—Å–æ–≤ –æ–±—ã—á–Ω–æ–≥–æ –∏–Ω–ø—É—Ç–∞
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

type SuperCheckboxPropsType = DefaultInputPropsType & {
  onChangeChecked?: (checked: boolean) => void;
  spanClassName?: string;
};

const SuperCheckbox: React.FC<SuperCheckboxPropsType> = ({
  type, // –¥–æ—Å—Ç–∞—ë–º –∏ –∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º —á—Ç–æ–± –Ω–µ–ª—å–∑—è –±—ã–ª–æ –∑–∞–¥–∞—Ç—å –¥—Ä—É–≥–æ–π —Ç–∏–ø –∏–Ω–ø—É—Ç–∞
  onChange,
  onChangeChecked,
  className,
  spanClassName,
  children, // –≤ —ç—Ç—É –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –ø–æ–ø–∞–¥—ë—Ç —Ç–µ–∫—Å—Ç, —Ç–∏–ø–∏–∑–∏—Ä–æ–≤–∞—Ç—å –Ω–µ –Ω—É–∂–Ω–æ —Ç–∞–∫ –∫–∞–∫ –æ–Ω –∑–∞—Ç–∏–ø–∏–∑–∏—Ä–æ–≤–∞–Ω –≤ React.FC

  ...restProps // –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –ø–æ–ø–∞–¥—É—Ç –≤ –æ–±—ä–µ–∫—Ç restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChangeChecked && onChangeChecked(e.currentTarget.checked);
  };

  const finalInputClassName = `${s.checkbox} ${className ? className : ""}`;

  return (
    <label>
      <input
        type={"checkbox"}
        onChange={onChangeCallback}
        className={finalInputClassName}
        {...restProps} // –æ—Ç–¥–∞—ë–º –∏–Ω–ø—É—Ç—É –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –µ—Å–ª–∏ –æ–Ω–∏ –µ—Å—Ç—å (checked –Ω–∞–ø—Ä–∏–º–µ—Ä —Ç–∞–º –≤–Ω—É—Ç—Ä–∏)
      />
      {children && <span className={s.spanClassName}>{children}</span>}
    </label> // –±–ª–∞–≥–æ–¥–∞—Ä—è label –Ω–∞–∂–∞—Ç–∏–µ –Ω–∞ —Å–ø–∞–Ω –ø–µ—Ä–µ–¥–∞—Å—Ç—Å—è –≤ –∏–Ω–ø—É—Ç
  );
};

export default SuperCheckbox;
