import { applyMiddleware, combineReducers, createStore } from 'redux'
import thunkMiddleware, { ThunkAction } from 'redux-thunk'
import {
	NewPasswordActionsType,
	newPasswordReducer,
} from './newPassword-reducer'
import {
	RegistrationActionsType,
	registrationReducer,
} from './registration-reducer'
import { AuthActionsType, authReducer } from './auth-reducer'
import { ProfileActionsType, profileReducer } from './profile-reducer'
import {
	PasswordRecoveryActionsType,
	passwordRecoveryReducer,
} from './passwordRecovery-reducer'
import { cardsReducer, CardsActionType } from './—Åards-reducer'
import { AppActionsType, appReducer } from './app-reducer'
import { PacksActionType, packsReducer } from './packs-reducer'

const rootReducer = combineReducers({
	app: appReducer,
	auth: authReducer,
	newPassword: newPasswordReducer,
	registration: registrationReducer,
	profile: profileReducer,
	passwordRecovery: passwordRecoveryReducer,
	cards: cardsReducer,
	packs: packsReducer,
})

export const store = createStore(rootReducer, applyMiddleware(thunkMiddleware))

// —á—Ç–æ–±—ã –º–æ–∂–Ω–æ –±—ã–ª–æ –≤ –∫–æ–Ω—Å–æ–ª–∏ –±—Ä–∞—É–∑–µ—Ä–∞ –æ–±—Ä–∞—â–∞—Ç—å—Å—è –∫ store –≤ –ª—é–±–æ–π –º–æ–º–µ–Ω—Ç
// @ts-ignore
window.store = store

//types
//—Ç–∏–ø –≤—Å–µ–≥–æ —Å—Ç–µ–π—Ç–∞
export type AppRootStateType = ReturnType<typeof rootReducer>
//—Ç–∏–ø –≤—Å–µ—Ö —ç–∫—à–µ–Ω–æ–≤ –∏–∑ –≤—Å–µ—Ö —Ä–µ–¥—å—é—Å–µ—Ä–æ–≤
export type AppRootActionsType =
	| NewPasswordActionsType
	| AuthActionsType
	| RegistrationActionsType
	| ProfileActionsType
	| PasswordRecoveryActionsType
	| AppActionsType
	| CardsActionType
	| PacksActionType
//–æ–±—â–∏–π —Ç–∏–ø –¥–ª—è –≤—Å–µ—Ö —Å–∞–Ω–æ–∫
export type AppThunk<ReturnType = void> = ThunkAction<
	ReturnType,
	AppRootStateType,
	unknown,
	AppRootActionsType
>
